<template>
    <header :class="{'login': isLogin, 'no-login': !isLogin}">
        <template v-if="!isLogin">
           <h1><router-link to="/">让我们一起分享吧！</router-link></h1>
           <p>精品博客汇聚</p>
           <div class="btns">
              <router-link to="/login"><el-button>立即登录</el-button></router-link>
              <router-link to="/register"><el-button>注册账号</el-button></router-link>
           </div>
        </template>
        <template v-if="isLogin">
           <h1><router-link to="/">让我们一起分享吧！</router-link></h1>
           <router-link to="/create"><i class="edit el-icon-plus"></i></router-link>
           <div class="user">
               <img class="avatar" :src="user.avatar" :alt="user.username" :title="user.username">
               <ul>
                   <li><router-link to="/my">我的</router-link></li>
                   <li><a href="#" @click="onLogout">注销</a></li>
               </ul>
           </div>
        </template>      
    </header>
</template>

<script>
  
  import { mapGetters, mapActions } from 'vuex'

  export default {
      data() {
          return {
          }
      },
      computed: {
          ...mapGetters([
              'isLogin',
              'user'
          ])
      },
      created() {
          this.checkLogin()
      },

      methods: {
          ...mapActions([
              'checkLogin',
              'logout'
          ]),
           onLogout() {
               this.logout()
             }
      }
  }
</script>

<style scoped>
header.no-login{
    padding: 0 12% 30px 12%;
    background: #149739;
    display: grid;
    justify-items: center;
}
header.no-login>h1>a{
    text-decoration: none;
    color: #fff;
}
header.no-login>h1 {
    font-size: 40px;
    margin: 60px 0 0 0;
    text-transform: uppercase;
}
header.no-login>p{
    margin: 15px 0 0 0;
    color: #fff;
}
header.no-login>.btns{
    margin: 20px 5px 0;
}
header.no-login>.btns>a {
    margin:0px 25px;
}
header.login{
    background: #149739;
    display: flex;
    justify-items: center;
}
header.login>h1>a{
    text-decoration: none;
    color: #fff;
}
header.login>h1 {
    margin: 0;
    padding: 0;
    font-size: 30px;
    text-transform: uppercase;
    flex: 1;
}
header.login>a>.edit{
    color: #fff;
    font-size: 30px;
    margin-top: 10px;
}
header.login>.user {
    position: relative;
}
header.login>.user>.avatar{
    width: 30px;
    height: 30px;
    border: 1px solid #fff;
    border-radius: 50%;
    margin-top: 7px;
    margin-left: 15px;
}
header.login>.user>ul{
    display: none;
    position: absolute;
    right: -7px;
    list-style: none;
    border: 1px solid #eaeaea;
    margin: 0;
    padding: 0;
    background: #fff;
}
header.login>.user>ul>li>a{
    text-decoration: none;
    color: #333;
    font-size: 12px;
    display: block;
    padding: 5px 10px;
}
header.login>.user>ul>li>a:hover{
    background: #eaeaea;
}
header.login>.user:hover ul {
    display: block;
}

</style>




