import Vue from 'vue'
import Router from 'vue-router'

import store from '../store'
window.store = store
Vue.use(Router)

/*
//以下是vue路由里的配置路径，方便模拟网址访问 （这样打包的时候，功能模块会被整合到一个js文件中，如果页面逻辑复杂就会造成单个js过大）块级1start...
import Index from '@/pages/Index/template.vue'
import Register from '@/pages/Login/template.vue'
import Login from '@/pages/Register/template.vue'
import User from '@/pages/User/template.vue'
import My from '@/pages/My/template.vue'
import Create from '@/pages/Create/template.vue'
import Edit from '@/pages/Edit/template.vue'
import Detail from '@/pages/Detail/template.vue'

const router = new Router({
        routes: [{
                path: '/',
                component: Index
            },
            {
                path: '/login',
                component: Login
            },
            {
                path: '/register',
                component: Register
            },
            {
                path: '/user/:userId',
                component: User
            },
            {
                path: '/my',
                component: My,
                meta: { requiresAuth: true }
            },
            {
                path: '/create',
                component: Create,
                meta: { requiresAuth: true }
            },
            {
                path: '/edit/:blogId',
                component: Edit,
                meta: { requiresAuth: true }
            },
            {
                path: '/detail/:blogId',
                component: Detail
            },
        ]
    })
    //...块级1end
*/

//(打包时模块分开，这样建立的js包就会按功能模块来加载，当实际发布访问就不会造成单个js文件过大)块级2start...
const router = new Router({
    routes: [{
            path: '/',
            component: () =>
                import ('@/pages/Index/template.vue')
        },
        {
            path: '/login',
            component: () =>
                import ('@/pages/Login/template.vue')
        },
        {
            path: '/register',
            component: () =>
                import ('@/pages/Register/template.vue')
        },
        {
            path: '/user/:userId',
            component: () =>
                import ('@/pages/User/template.vue')
        },
        {
            path: '/my',
            component: () =>
                import ('@/pages/My/template.vue'),
            meta: { requiresAuth: true }
        },
        {
            path: '/create',
            component: () =>
                import ('@/pages/Create/template.vue'),
            meta: { requiresAuth: true }
        },
        {
            path: '/edit/:blogId',
            component: () =>
                import ('@/pages/Edit/template.vue'),
            meta: { requiresAuth: true }
        },
        {
            path: '/detail/:blogId',
            component: () =>
                import ('@/pages/Detail/template.vue')
        },
    ]
})

router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) {
        store.dispatch('checkLogin').then(isLogin => {
            if (!isLogin) {
                next({
                    path: '/login',
                    query: { redirect: to.fullPath }
                })
            } else {
                next()
            }
        })
    } else {
        next() // 确保一定要调用 next()
    }
})

export default router


